/**
 * Mixins
 */

// Clearfix
// --------
// For clearing floats like a boss h5bp.com/q
@mixin clearfix {
  *zoom: 1;
  &:before,
  &:after {
    display: table;
    content: "";
    // Fixes Opera/contenteditable bug:
    // http://nicolasgallagher.com/micro-clearfix-hack/#comment-36952
    line-height: 0;
  }
  &:after {
    clear: both;
  }
}

/* ----------------------------------------------------------------------------
* Responsive Media Queries Mixin

* Usage
@include r(240)  { }
@include r(320)  { }
@include r(480)  { }
@include r(768)  { }
@include r(1024) { }
@include r(1140) { }
@include r(1280) { }

* ------------------------------------------------------------------------- */

@mixin r($point) {
    @if $point==240 {
        @media (min-width: 240px) {
            @content;
        }
    }
    @if $point==320 {
        @media (min-width: 320px) {
            @content;
        }
    }
    @if $point==480 {
        @media (min-width: 480px) {
            @content;
        }
    }
    @if $point==600 {
        @media (min-width: 600px) {
            @content;
        }
    }
    @if $point==768 {
        @media (min-width: 768px) {
            @content;
        }
    }
    @if $point==1024 {
        @media (min-width: 1024px) {
            @content;
        }
    }
    @if $point==1140 {
        @media (min-width: 1140px) {
            @content;
        }
    }
    @if $point==1280 {
        @media (min-width: 1280px) {
            @content;
        }
    }
    @if $point==1500 {
        @media (min-width: 1500px) {
            @content;
        }
    }
}
